<template>
    <div class="orderDetail_container">
        <head-top goBack="true" head-title="订单详情"></head-top>
        <section v-if="!showLoading" id="scroll_section" class="scroll_container">
            <section class="scroll_insert">
                <img :src="imageBaseUrl+orderDateil.restaurant_image_url" alt="">
                <p class="">{{orderDateil.status_bar.title}}</p>
                <p>{{orderDateil.timeline_node.description}}</p>
                <router-link class="order_again" :to="{path:'/shop',query:{geohash,id:orderDateil.restaurant_id}}">再来一单</router-link>
            </section>
            <section class="food_list">
                <router-link class="food_list_header" :to="{path:'/shop',query:{geohash,id:orderDateil.restaurant_id}}">
                    <div class="shop_name">
                        <img :src="imageBaseUrl+orderDateil.restaurant_image_url" alt="">
                        <span>{{orderDateil.restaurant_name}}</span>
                    </div>
                    <svg fill="#333" class="arrow_right">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg>
                </router-link> 
            </section>
                <ul class="food_list_ul">
                    <li v-for="item in orderDateil.basket.group[0]" :key="item.id">
                        <p class="food_name ellipsis"> {{item.name}}</p>
                        <div class="quantity_price">
                            <span>x{{item.quantity}}</span>
                            <span>￥{{item.price}}</span>
                        </div>
                    </li>
                </ul>
                <div class="deliver_fee">
                    <span>配送费</span>
                    <span>{{orderDateil.basket.deliver_fee&&orderDateil.basket.deliver_fee.price || 0}}</span>
                </div>
                <div class="pay_ment">
                    实付{{orderDateil.total_amount.toFixed(2)}}
                </div>
                        <section class="order-detail-style">
                <header>配送信息</header>
                <section class="item_style">
                    <p class="item_left">送达时间 : </p>
                    <div class="item_right">
                        <p>{{orderData.deliver_time}}</p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">送货地址 : </p>
                    <div class="item_right">
                        <p>{{orderData.consignee}}</p>
                        <p>{{orderData.phone}}</p>
                        <p>{{orderData.address}}</p>
                    </div>
                </section>
            </section>
            <section class="order-detail-style">
                <header>订单信息</header>
                <section class="item_style">
                    <p class="item_left">订单号</p>
                    <div class="item_right">
                        <p>{{orderDateil.id}}</p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">支付方式</p>
                    <div class="item_right">
                        <p>在线支付</p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">下单时间</p>
                    <div class="item_right">
                        <p>{{orderDateil.formatted_created_at}}</p>
                    </div>
                </section>
            </section>
        </section>
        <loading v-show="showLoading"></loading>
    </div>
</template>

<script>
import headTop from '../../../components/header/header'
import {mapState,mapmutations, mapMutations} from 'vuex'
import loading from '../../../components/common/loading'
import Bscroll from 'better-scroll'
import {imageBaseUrl} from '../../../config/env'
import {getOrderDetail} from '../../../service/getData'
import {getImgPath} from '../../../components/common/mixin'
export default {
    components: {
        headTop,
        loading
    },
    mixins: [getImgPath]
    ,
    created () {
       
    },mounted () {
        this.initdata();
    },computed: {
        ...mapState([
            'orderDateil','geohash','userInfo'
        ])
    },methods: {
       async initdata()
       {
           if(this.userInfo && this.userInfo.user_id)
           {
               this.orderData=await getOrderDetail(this.userInfo.user_id,this.orderDateil.unique_id);
               this.showLoading=false;
               console.log(JSON.stringify( this.orderData));
               //$nextTick等所有事件完成之后再执行
               this.$nextTick(()=>{
                   new Bscroll('#scroll_section',{
                       deceleration:0.001,
                       bounce:true,
                       swipeTime:1800,
                       click:true
                   })
               })
           }
       }
    },data(){
        return{
            showLoading:true,//显示加载动画
            orderData:null,
            imageBaseUrl     
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.orderDetail_container{
    position: fixed;
    top:0;
    left:0;
    bottom:0;
    right: 0;
    background-color:#f5f5f5;
    z-index: 103;
    overflow-y: auto;
}
.scroll_container{
    margin-top: 2rem;
    .scroll_insert{
        background: #fff;
        display: flex;
        flex-direction: column;
        padding:.7rem;
        align-items: center;
        justify-content: center;
        img{
            @include wh(3rem,3rem);
            border:0.025rem solid $blue;
            border-radius: 50%;
        }
        p:nth-of-type(1)
        {
            margin-top: 0.6rem;
            @include sc(0.9rem,#333);
        }
        p:nth-of-type(2)
        {
            @include sc(.55rem, #999);
            width: 10rem;
            margin-top: .3rem;
            text-align: center;
        }
       .order_again{
           margin-top: 0.3rem;
           border:0.025rem solid $blue;
           border-radius: 0.2rem;
           @include sc(0.65rem,$blue);
           padding:0.2rem;

       }
    }
}
.food_list{
    margin-top: 0.5rem;
    background: #fff;
    .food_list_header{
        display: flex;
        justify-content: space-between;
        border-bottom: 0.025rem solid $bc;
        padding:0.3rem;
        .shop_name{
            display: flex;
            align-items: center;
            img{
                @include wh(1.5rem,1.5rem);
            }
            span{
                padding-left: 0.4rem;
                @include sc(0.6rem,#333);
            }
            
        }
        svg{
            margin-top: 0.4rem;
            @include wh(.5rem,.5rem);
            line-height: 10.4rem;
        }
    }
}
.food_list_ul{
background: #fff;
    li{
        border-bottom:0.025rem solid $bc;
        display: flex;
        padding:0.4rem;
        justify-content: space-between;
        align-items: center;
        .food_name{
            @include sc(0.6rem,#666);
        }
        .quantity_price{
            display: flex;
            align-items: center;
            span:nth-of-type(1)
            {
                padding-right: 0.8rem;
                font-size: 0.6rem;
                
            }
            span:nth-of-type(2)
            {
                color:#f60;
                font-size: 0.6rem;
            }
        }
    }
}
.deliver_fee{
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding:0.4rem;
    align-items: center;
    border-bottom:0.025rem solid $bc;
    span{
          @include sc(0.6rem,#666);
    }
}
.pay_ment{
    background: #fff;
    display: flex;
    align-items:center;
    @include sc(0.6rem,#f60);
    justify-content: flex-end;
    padding:0.4rem;
    border-bottom: 0.025rem solid #f1f1f1;
}
.order-detail-style{
    margin-top: 0.5rem;
    background: #fff;
    header{
        @include sc(0.7rem,#666);
        padding:0.4rem;
        border-bottom: 0.025rem solid $bc;
    }
    .item_style{
        display: flex;
        padding:0.4rem;
        border-bottom:0.025rem solid $bc;
        align-items: center;
        .item_left{
           @include sc(0.6rem,#666); 
        }
        .item_right{
            display: flex;
            align-items: center;
            p{
                padding-left:0.4rem;
                @include sc(0.6rem,#666); 
            }
        }
    }
}
</style>



